<template>

    <div
        class="icon"
        :style="{
            width: background ? size + 'rem' : '',
            height: background ? size + 'rem' : '',
            backgroundColor: background ? background : '',
            borderRadius: rounded ? size / 2 + 'rem' : '0'
        }"
    >

    <svg :style="{width: sizeValue, height: sizeValue, fill: color}">
        <use :xlink:href="'./svg/main.svg#' + icon"/>
    </svg>

    </div>
    
</template>

<script>

    import styles from '../../styles/variables'
    import check from '../../svg/check.svg'
    require.context('../../svg', false, /\.svg$/)

//var id = require('svg-sprite-loader!../../svg/check.svg');

    export default {
        computed: {
            iconSize: function() {
                return this.background ? this.size * 0.6 : this.size
            }
        },
        props: {
            icon: {required: true},
            size: {default: 'md'},
            color: {default: 'black'},
            background: {default: ''},
            rounded: {default: true}
        },

        computed: {
            sizeValue: function() {
                return styles.iconSizes[this.size]
            }
        }
    }

</script>


